import React, { useEffect, useState } from 'react';
import {
  Switch,
  Route,
  useLocation
} from 'react-router-dom';

import './css/style.scss';

import AOS from 'aos';
import { focusHandling } from 'cruip-js-toolkit';

import Home from './pages/Home';
import SignIn from './pages/SignIn';
import Compose from './pages/Compose';
import Article from './pages/Article';
import { apiGetUser } from './utils/network';
import UserArticlesPage from './pages/UserArticlesPage';

export const UserContext = React.createContext(null);

const KEY_TOKEN = 'token'

function App() {
  const token = localStorage.getItem(KEY_TOKEN)

  const [user, setUser] = useState({
    token: token ?? "",
  });

  useEffect(() => {
    let isMounted = true;

    if (token) {
      const refresh = async () => {
        const currentUser = await apiGetUser(token)

        if (!isMounted) return;

        if (currentUser) {
          currentUser.token = token;
          setUser(currentUser);
        } else {
          localStorage.removeItem(KEY_TOKEN);
        }
      };
      refresh();

      return () => { isMounted = false };
    }
  }, [token])

  const login = (user) => {
    setUser(user);
    localStorage.setItem(KEY_TOKEN, user.token);
  }

  const logout = () => {
    setUser({ token: "" });
    localStorage.removeItem(KEY_TOKEN);
  }

  const location = useLocation();

  useEffect(() => {
    AOS.init({
      once: true,
      // disable: 'phone',
      duration: 700,
      easing: 'ease-out-cubic',
    });
  });

  useEffect(() => {
    document.querySelector('html').style.scrollBehavior = 'auto'
    window.scroll({ top: 0 })
    document.querySelector('html').style.scrollBehavior = ''
    focusHandling('outline');
  }, [location.pathname]); // triggered on route change

  return (
    <UserContext.Provider value={{ user: user, login: login, logout: logout }}>
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>

        <Route exact path="/articles/by/:userId?">
          <UserArticlesPage user={user} />
        </Route>

        <Route path="/signin">
          <UserContext.Consumer>
            {({ user, login, _ }) => (
              <SignIn user={user} login={login} />
            )}
          </UserContext.Consumer>
        </Route>
        <Route path="/compose/:postId?">
          <UserContext.Consumer>
            {({ user, _, __ }) => (
              <Compose user={user} />
            )}
          </UserContext.Consumer>
        </Route>
        <Route path="/article/:id">
          <UserContext.Consumer>
            {({ user, _, __ }) => (
              <Article user={user} />
            )}
          </UserContext.Consumer>
        </Route>
      </Switch>
    </UserContext.Provider>
  );
}

export default App;
